.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.scrollmenu {
  background-color: #f44336;
  overflow: auto;
  white-space: nowrap;
}

.scrollmenu a {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 14px;
  text-decoration: none;
}

.scrollmenu a:hover {
  background-color: #777;
}

.Bloque {
  width: 100vw;
  max-width: 640px;
  height: 100vh;
  margin: 0;
  padding-top: 2vh;
  padding-bottom: 2vh;
  padding-left: 2vw;
  padding-right: 2vw;
  background-color: crimson;
  display: block;
}

.Titulo {
  background-color: darkgrey;
  width: 80vw;
  max-width: 512px;
  height: 10vh;
  position: absolute;
  left: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Times New Roman", Times, serif;
  font-weight: bold;
  font-size: 5vw;
}

.Menu {
  width: 80vw;
  max-width: 512px;
  position: absolute;
  top: 11vh;
  left: 10vw;
  overflow-x: scroll;
}

.Cuerpo {
  background-color: whitesmoke;
  width: 80vw;
  max-width: 512px;
  height: 84vh;
  position: absolute;
  left: 10vw;
  margin-top: 12vh;
  overflow-y: scroll;
}

.Boton {
  background-color: thistle;
  display: inline-block;
  width: 32.5vw;
  max-width: 208px;
  height: 25vh;
  margin-left: 5vw;
  margin-top: 2.25vh;
}

.Barra {
  background-color: rgb(105, 99, 105);
  height: 7vh;
  position: absolute;
  left: 10vw;
  bottom: 2vh;
  display: flex;
  justify-content: center;
}

.Ancho {
  width: 80vw;
  max-width: 512px;
}

.Botoncito {
  width: 16vw;
  max-width: 102.4px;
  flex: 1;
  color: rgba(255, 255, 255, 0.7);
  padding: 6px 12px 8px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    padding-top 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

@media screen and (min-width: 640px) {
  .Bloque {
    padding-left: 12.8px;
    background-color: teal;
  }
  .Titulo {
    position: absolute;
    left: 64px;
  }
  .Menu {
    position: absolute;
    left: 64px;
  }
  .Cuerpo {
    position: absolute;
    left: 64px;
  }
  .Barra {
    position: absolute;
    left: 64px;
  }
  .Boton {
    margin-left: 32px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.pegado {
  display: flex;
  flex-wrap: wrap;
}

.izq {
  margin-right: 5px;
}
.shrink {
  display: flex;
  height: auto;
  font-size: 1.7vw;
}
.App-link {
  color: #61dafb;
}
.scrolling-wrapper {
  position: relative;
  display: inline-block;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  vertical-align: top;
}

.card {
  flex: 0 0 auto;
}

.dropbtn {
  background-color: rgb(155, 156, 15, 0.3);
  color: grey;
  width: 100%;
  padding: 2%;
  font-size: 16px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.dropdown {
  width: 23%;
  margin: 1%;
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: rgba(249, 249, 249, 0.8);
  width: 100%;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a,
button {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a,
button:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #3e8e41;
  color: white;
}

.rubros {
  width: 99%;
  height: 10%;
  margin: 0.5%;
  padding: 1%;
}
.nombres {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: white;
  overflow: hidden;
}
.texto {
  padding-left: 1%;
  width: 200%;
  height: 100%;
}

.iconos {
  display: inline-block;
  width: 40%;
  height: 100%;
  background-color: antiquewhite;
  overflow: hidden;
}
.nombres:hover {
  background-color: rgba(204, 171, 171, 0.801);
  overflow: visible;
}

.nombres:hover + .iconos {
  background-color: rgba(255, 255, 255, 0.3);
  width: 20%;
}
.categoria {
  width: 100%;
}
.categoria:hover {
  cursor: pointer;
}

.bloqArriba {
  width: 96%;
  height: 49%;
  margin: 2%;
  padding: 1%;
  background-color: rgba(255, 255, 255, 0.7);
}
.bloqAbajo {
  width: 96%;
  height: 49%;
  margin: 2%;
  padding: 1%;
  background-color: rgba(255, 255, 255, 0.7);
}

.datosUsuarios {
  width: 100%;
  height: 85%;
  overflow: auto;
}

.cartaUsuario {
  width: 100%;
  height: 10%;
  margin: 1% 0% 1% 0%;
  padding: 1%;
  overflow: hidden;
  display: inline-block;
}
.reportes {
  display: none;
  position: absolute;
  right: 0;
  background-color: rgba(249, 249, 249, 0.8);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.reportes a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.cartaUsuario:hover {
  background-color: silver;
}
.cartaUsuario:hover .reportes {
  display: block;
}

.reportes a:hover {
  background-color: lightgrey;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
